    import back from '@/components/back'
	import noData from '@/components/noData'
    export default {
        components: {
			back,
			noData
        },
        data () {
            return {
                imgs: [require("../../assets/default/doctor.png"), require("../../assets/default/doctor.png")],
                id: this.$route.query.id,
                datas: {},
                user: this.$store.state.userInfo.Data
            }
        },
        mounted() {
            this.getFaceConsultation()
        },
        methods: {
            toIM () {
                let o = {}
				 o.PatientInfo = {
                    PatientId: this.datas.PatientId,
                    PatientName: this.datas.PatientName,
                    PatientSex: this.datas.PatientSex
                }
                o.ApplicantHeadImgUrl = this.datas.PatientPic
                o.ApplicantIMId = this.datas.ApplicantIMId
                o.ApplicantId = this.datas.PatientAccountId
                this.$bridge.callhandler('SendMessage', JSON.stringify(o))
            },
            getFaceConsultation () {
                this.$get('AppointmentAPI/FaceConsultation/GetFaceConsultationDetail',{
                    RecordId: this.id
                }).then( m => {
                    this.datas = m
                })
            },
            cancleHandel () {
                this.$post('AppointmentAPI/FaceConsultation/CancelFaceConsultation',{
                    RecordId: this.datas.RecordId,
                    CancelType: 2,
                    PatientIdOrDrId: this.user.User.DoctorId
                }).then( m => {
                    if(m) {
                        this.$toast('取消成功！')
                        this.getFaceConsultation()
                    }
                    
                    
                })
            }
        },
    }



